.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

@media (width <= 768px) {
  .header {
    padding: 0 20px;
    font-size: 10px;
  }
}

.header__logo {
  flex-shrink: 0;
  width: 40px;
  height: 40px;
}

.header__nav-list {
  list-style: none;
  display: flex;
}

.header__nav-item {
  cursor: pointer;
}

.header__nav-item:not(:first-child) {
  margin-left: 20px;
}

.header__nav-link {
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.header__nav-link.is-active, .header .header__nav-link:hover {
  color: #00acdc;
}

.header__nav-link.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.product-card {
  grid-template-columns: repeat(1, 200px);
  grid-auto-flow: row;
  justify-content: center;
  align-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .product-card {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .product-card {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .product-card {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 0 16px;
  display: grid;
}

.card:hover {
  color: #34568b;
  transform: scale(1.2);
}

.card__img {
  flex-shrink: 0;
  width: 160px;
  height: 134px;
  margin: 32px 0 40px 3px;
}

.card__title {
  color: #060b35;
  width: 166px;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  text-align: center;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 24px;
  display: flex;
}

.card__price-price {
  color: #616070;
  justify-content: center;
  font-size: 12px;
  line-height: 18px;
  display: flex;
}

.card__price-text {
  color: #060b35;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  display: flex;
}

.card__button {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  flex-shrink: 0;
  width: 166px;
  height: 40px;
  margin: 16px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  text-align: center;
  text-transform: uppercase;
  background: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__reviews {
  text-align: center;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 16px;
  display: flex;
}

.card__reviews-stars-4 {
  display: flex;
}

.card__reviews-reviews {
  color: #060b35;
  justify-content: center;
  align-items: center;
  padding-top: 1px;
  font-size: 10px;
  font-weight: 300;
  line-height: 14px;
  display: flex;
}

.card__stars-star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.card__stars-star.card__stars-star:not(:last-child) {
  margin-right: 4px;
}

.card__reviews-stars-4 .card__stars-star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

